body, .app {
    background: black;
    color: white;
}

.app{
    display: -ms-flexbox;
    display: flex;
    height: 100%;
}

.sidebar {
    -ms-flex: 2 1;
        flex: 2 1;
}

.map {
    -ms-flex: 5 1;
        flex: 5 1;
}

.sidebar .filter-results {
    height: 90vh;
    overflow: scroll;
}

/* Responsive for Mobile */

@media only screen and (max-width: 775px) {
    .app {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .sidebar .filter-results {
        height: 30vh;
        overflow: scroll;
    }
}


